<template>
<div class="bottombar">
<div class="left">
<div class="btn" @click="btnclicked"
:class="{activestyle:isactive}">
<span class="zi zi_check fontimg" v-if="isactive" >
</span>
</div>
<span class="all">全选</span>
<span>合计:{{totalprice}}</span>
</div>

<div class="right" v-if="buynumber">
去结算({{buynumber}})
</div>
</div>
</template>

<script>
export default {
    data:function(){
        return{
           
              }
    },
    props:{
       isactive:{
           type:Boolean,
           default:false
       }
    },
  methods:{
   btnclicked:function(){
   this.$emit("clicked");
  
   },
    },
    computed:{
    totalprice:function(){
     let allprice=0;
     for(let value of this.$store.state.cartlist)
     {
       if(value.ischosen)
       allprice+=value.price*value.count;
     }
     return allprice>0?"￥"+allprice:"";
      },
      buynumber:function(){
    let num=0;
    for(let value of this.$store.state.cartlist)
     {
       if(value.ischosen)
       num++;
     }
     return num>0?num:undefined;
    }
    },
    
}
</script>

<style scoped>
.bottombar{
    width: 100%;
    display: flex;
    line-height: 36px;
    justify-content: space-between;
    align-items: center;
    text-align: center;
    position: absolute;
    bottom: 64px;
    background-color: rgb(235, 232, 232);
}
.left{
    display: flex;
    align-items: center;
}
.btn{
    width: 15px;
    height: 15px;
    border:1px solid rgb(114, 111, 111);
    border-radius: 50%;
    line-height: 15px;
    margin-left: 5px;
}
.btn .fontimg{
    color: white;
}
.activestyle{
    border-color: rgb(245, 149, 165);
    background-color:rgb(245, 149, 165) ;
}
.left span{
    color: rgb(168, 164, 164);
}
.all{
    margin:0 18px 0 3px;
    font-size: 13px;
}
.right{
    background-color: rgb(250, 143, 161);
    width: 23%;
    color: white;
}
</style>