<template>
<div class="shoppingcart">
<!-- 顶部栏 -->
<top-bar>
<div slot="center" class="center">
购物车({{$store.state.cartlist.length}})
</div>
</top-bar>

<!-- 购物车列表 -->
<cart-list  @clicked="singleclicked" />



<bottom-bar :isactive="allistrue" @clicked="allclicked"/>
</div>
</template>

<script>
import topbar from "../../common/topnavbar.vue"
import cartlist from "./cartlist.vue"
import bottombar from "./bottombar.vue"
import Bottombar from '../detail/bottombar.vue'

export default {
  components:{
      topBar:topbar,
      cartList:cartlist,
      bottomBar:bottombar,
       
  },
  data:function(){
    return{
      allistrue:false,
    }
  },
  methods:{
    allclicked:function(){
       this.allistrue=!this.allistrue;
   for(var value of this.$store.state.cartlist)
   {
       value.ischosen=this.allistrue;
   }

    },
    singleclicked:function(){
      let flag=true;
      for(var value of this.$store.state.cartlist)
       {   
           if(!value.ischosen)
           {
                this.allistrue=false;
                flag=false;
           }
       }
       if(flag)
       this.allistrue=true
    }
  }
}
</script>

<style  scoped>
.center{
    width: 70%;
}



</style>
